<template>
    <div style="width:90%;">
        <div class="mb-6">
            <h2>Mis clientes</h2>
        </div> 
        <div>
            <v-form>
                <v-row
                v-for="(cliente, index) in clientes" 
                no-gutters
                class="mb-2"
                :key="cliente.id">
                    <v-btn style="width:82%" @click="push(cliente.id)">
                        {{ cliente.nombre }} {{ cliente.apellido }}
                    </v-btn>
                    <v-btn style="width:4%" color="red" @click="accionEliminar(index)">
                        <v-icon>mdi-delete</v-icon>
                    </v-btn>
                </v-row>
            </v-form>
        </div>
        <div class="d-flex justify-end mt-6">
            <v-btn  @click="abrirDialog">
                <v-icon>mdi-plus</v-icon>
                <span>AÃ±adir Nuevo</span>
            </v-btn>
        </div>
        <div>
            <v-dialog 
            v-model="dialog"
            >
                <v-card class="pa-4">
                    <v-form class="pa-4">
                        <v-row>
                            <v-text-field
                            label="Nombre"
                            v-model="clienteTemplate.nombre">
                            </v-text-field>
                        </v-row>
                        <v-row>
                            <v-text-field
                            label="Apellido"
                            v-model="clienteTemplate.apellido">
                            </v-text-field>
                        </v-row>
                        <v-row>
                            <v-text-field
                            label="Correo"
                            v-model="clienteTemplate.correo">    
                            </v-text-field>
                        </v-row>
                        <v-row>
                            <v-text-field
                            label="Direccion"
                            v-model="clienteTemplate.direccion">    
                            </v-text-field>
                        </v-row>
                        <v-row>
                            <v-text-field
                            label="Telefono"
                            v-model="clienteTemplate.telefono">    
                            </v-text-field>
                        </v-row>
                    </v-form>
                    <div>
                        <v-row class="pa-4">
                            <v-btn @click="cerrarDialog" >
                                <v-icon>mdi-close-box</v-icon>
                                Cancelar
                            </v-btn>
                            <v-spacer></v-spacer>
                            <v-btn @click="accionGuardar" >
                                <v-icon>mdi-content-save-edit</v-icon>
                                Guardar
                            </v-btn>
                        </v-row>
                    </div>
                </v-card>
            </v-dialog>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            clienteTemplate:{
                id: 0,
                nombre: '',
                apellido: '',
                correo: '',
                direccion: '',
                telefono: '',
                idSemana: 0
            },
            dialog: false
        }
    },
    methods:{
        abrirDialog(){
            this.dialog = true
        },
        accionGuardar(){
            this.$store.commit('guardarInfoCliente', {
                id: 0,
                nombre: this.clienteTemplate.nombre,
                apellido: this.clienteTemplate.apellido,
                correo: this.clienteTemplate.correo,
                direccion: this.clienteTemplate.direccion,
                telefono: this.clienteTemplate.telefono,
                idSemana: this.clienteTemplate.idSemana
            })
            this.cerrarDialog()
        },
        accionEliminar(index){
            this.$store.commit('eliminarCliente',index)
        },
        cerrarDialog(){
            this.dialog = false,
            this.clienteTemplate = {
                id: 0,
                nombre: '',
                apellido: '',
                correo: '',
                direccion: '',
                telefono: '',
                idSemana: 0
            }
        },
        push(clienteId) {
            this.$router.push(`${this.$router.currentRoute._rawValue.path}/${clienteId}`)
            return 
        }
    },
    computed:{
        clientes(){
            return this.$store.getters.listadoClientes
        } 
    },
    }

</script>

